@import "base/variables";
@import "mixins/importMixins";
@import "base/font";
@import "modules/jQueryScrollbar";
@include meyer-reset();
.scrollbar-dynamic {
  //max-height: 300px;
  overflow: auto;
}
html,body{
  width: 100%;
  overflow: hidden;
}

.clearfix {
  @include clearfix();
}

.bg-no-repeat{
  background: no-repeat 0 0;
}

.logo-with-text {
  @extend .bg-no-repeat;
  @media (max-width: 1024px) {
    display: none !important;
  }
  width: 231px;
  height: 55px;
  background-image: url('data:image/png;base64,');
}

.logo {
  @extend .bg-no-repeat;
  width: 48px;
  height: 48px;
  display: none !important;
  @media (max-width: 1024px) {
    display: inline-block !important;
  }
  background-image: url('data:image/png;base64,');
}

.header{
  position: fixed;
  height: 56px;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: $gray-20;
  >[class^="icon-"], >[class*=" icon-"]{
    font-size: ceil($font-size-large*2.2);
    color: lighten($prime-blue,25%);
    border-radius: ceil($font-size-large*2.2);
    @include css3("transition",all 0.3s);
    &:hover{
      color: lighten($prime-blue,50%);
      background-color: $prime-blue;
    }
  }
  >span{
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }
  @media (max-width: 1024px) {
    height: 48px;
  }
}

.footer{
  @extend .header;
  background-color: transparent;
  position: fixed;
  top: 15px;
  bottom: auto;
  left: auto;
  right: 15px;
  >[class^="icon-"], >[class*=" icon-"]{
    font-size: ceil($font-size-large*1.8);
    margin-left: 5px;
  }
  @media (max-width: 1024px) {
    height: 37px;
    background-color: $gray-98;
    @include border("border-top");
    padding: 5px;
    top: auto;
    bottom: 0;
    left: 0;
    right: 0;
    height: auto;
    >[class^="icon-"], >[class*=" icon-"]{
      color: $toolbar-set;
      &:hover{
        color: $gray-100;
      }
    }
  }
}

/*torrent-list start*/
.torrent-list{
  position: fixed;
  top: 56px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background-color: $gray-30;
  >li{
    list-style: none;
    @include yahei();
    padding: 10px;
    @include border("border-bottom",1px,lighten($gray-0,35%));
    @include css3("transition",all 0.3s);
    cursor: pointer;
    &:hover{
      background-color: lighten($gray-0,33%);
    }
    &:last-child{
      border: none;
    }
    >.progess-bar{
      height: 3px;
      background-color: lighten($gray-0,40%);
      >div{
        height: 3px;
        @include css3("transition",all 0.5s);
      }
    }
    >.status,>.transform-data{
      font-size: $font-size-base;
      color: lighten($gray-0,60%);
      >span{
        margin: 0 0 0 10px;
      }
    }
    >.name,>.status,>.progess-bar{
      margin-bottom: 10px;
    }
    >.name{
      color: lighten($gray-0,90%);
      word-break: keep-all;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      min-width: 0;
    }
    >.transform-data{
      //>span{
      //  margin: 0 0 0 10px;
      //}
    }
    &.downloading{
      >.progess-bar{
        >div{
          background-color: $prime-blue;
          animation: downloading 1.5s infinite;
        }
      }
    }
    &.seeding{
      >.progess-bar{
        >div{
          background-color: $prime-green;
        }
      }
    }
    &.paused{
      >.progess-bar{
        >div{
          background-color: lighten($gray-0,24%);
          //animation: downloading 1.5s infinite;
        }
      }
    }
    &.selected{
      background-color: lighten($gray-0,45%);
      >.name{
        color: lighten($gray-0,100%);
      }
      >.status,>.transform-data{
        color: lighten($gray-0,80%);
      }
    }
  }
  @media (max-width: 1024px) {
    top: 48px;
    bottom: 37px;
  }
}

@keyframes downloading
{
  0% {
    background-color: $prime-blue;
  }
  50% {
    background-color: lighten($prime-blue,10%);
  }
  100% {
    background-color: $prime-blue;
  }
}
/*torrent-list end*/

.torrent-detail{
  position: fixed;
  top: 56px;
  right: -570px;
  bottom:0;
  background-color: rgba(51,51,51,0.85);
  opacity: 0;
  width: 570px;
  @include css3("transition",all 0.5s);
  box-shadow: none;
  &.show{
    right: 0;
    opacity: 1;
    box-shadow: -5px 5px 20px rgba(51,51,51,0.75);
  }
  @media (max-width: 1024px) {
    top: 48px;
    bottom: 37px;
    width: 100%;
    right: 0;
  }
  >.icon-close{
    color: $gray-100;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
  }
  >p{
    text-align: center;
    margin: 20px auto;
    >span{
      box-sizing: border-box;
      display: inline-block;
      border-radius: ceil($font-size-large*2.2);
      font-size: ceil($font-size-large*1.58);
      cursor: pointer;
      margin: 0 15px;
      color: $gray-70;
      &:hover{
        color: $gray-100;
      }
      &.selected{
        color: $gray-100;
        background-color: $prime-blue;
      }
    }
  }
  >ul{
    //width: 200px;
    margin: 20px auto;
    @extend .clearfix;
    border-radius: 5px;
    text-align: center;
    >li{
      display: inline-block;
      box-sizing: border-box;
      width: 25%;
      float: left;
      color: $gray-70;
      text-align: center;
      @include css3("transition",all 0.5s);
      >span{
        border-radius: ceil($font-size-large*2.2);
        padding: 0 10px;
        font-size: ceil($font-size-large*1.58);
        cursor: pointer;
      }
      &:hover{
        color: $gray-100;
      }
      &.selected{
        color: $gray-100;
        background-color: $prime-blue;
      }
    }
  }
}